.signin {
  display: flex;
  margin-top: 50px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: "Amazon Ember", Arial, sans-serif;
}

.signin__container {
  border: 1px solid blue;
  border-radius: 4px;
  border: 1px solid #999;
  padding: 24px 48px;
  /* width: 400px; */
  margin-top: 20px;
}
.signin__label {
  font-weight: 400;
}

.signin__container h2 {
  margin-bottom: 20px;
  font-weight: 500;
}

.signin__button {
  background-color: #febd69 !important;
  border-radius: 4px;
  border: none;
  padding: 8px;
  width: 100%;
  outline: none !important;
  text-transform: capitalize !important;
}

input {
  outline: none !important;
  /* width: 280px !important; */
  outline-color: none !important;
}

input:focus,
input:active {
  outline-width: 0px !important;
  outline-color: #febd69 !important;
}

Link {
  text-decoration: none !important;
}

.signin__captch {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  /* padding: 10px 0; */
  margin: 10px 0;
}
.signin__captch button {
  padding: 11px;
  background-color: #d2e5dd;
  border-radius: 4px;
  border: none;
  outline: none;
}

.signin__captch button:focus {
  outline: none;
}
.captcha {
  /* border: 1px solid black; */
  padding: 10px;
  border-radius: 4px;
  flex: 1;
  text-align: center;
  background: url("./back.jpg");
  color: #fff;
  font-weight: 600;
  margin-right: 4px;
  object-fit: cover;
  font-size: 19px;
}
